/*=======================END-MEDIA=====================*/
/* media breakpoint mixin */
.hero {
  display: flex;
  height: calc(100 * var(--vh));
}

.hero .container, .hero__inner {
  display: flex;
  height: 100%;
}

.hero__inner {
  align-items: center;
  position: relative;
}

.hero__title {
  font-weight: 400;
  text-align: center;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.hero__button {
  font-weight: 500;
  position: absolute;
  transform: translateX(-50%);
  left: 50%;
  bottom: 30px;
  font-size: 12px;
  line-height: 1.33;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-transform: uppercase;
  transition-timing-function: ease-in-out;
  transition-duration: 300ms;
  transition-property: all;
}

@media (any-hover: hover) {
  .hero__button:hover {
    transform: translate(-50%, -5px);
  }
}

.hero__button .icon {
  margin-top: 5px;
  transform: rotate(90deg);
  height: 13px;
  width: 7px;
  object-fit: cover;
  object-position: center;
}
